@import url('https://fonts.googleapis.com/css2?family=Tajawal:wght@400;700&display=swap');

/* Page layout */
.home-root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(180deg, #b30000, #e60000);
    font-family: 'Tajawal', sans-serif;
    text-align: center;
    direction: rtl;
    gap: 40px;
    padding: 20px;
}

/* Restaurant title */
.brand h1 {
    font-size: 52px;
    color: white;
    font-weight: 700;
    letter-spacing: 1px;
    margin-bottom: 10px;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

/* Highlight "بصرة" */
.tag .highlight {
    font-weight: 900;
    font-size: 36px;
    color: #FFD700;
    /* gold */
    text-shadow: 0 0 10px #FFD700, 0 0 20px #FFD700, 0 0 30px rgba(255, 215, 0, 0.5);
    animation: pulse 1.2s infinite;
}

@keyframes pulse {

    0%,
    100% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.15);
    }
}

/* Tagline */
.brand .tag {
    font-size: 24px;
    font-weight: 500;
    color: #ffe6e6;
    margin-bottom: 10px;
}

.brand h3 {
    font-size: 24px;
    color: white;
    font-weight: 600;
}

/* Circle buttons container */
.circle-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 22px;
}

/* Circle button common style */
.circle-btn {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-weight: bold;
    color: white;
    text-align: center;
    padding-bottom: 6px;
    background-size: cover;
    background-position: center;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.25);
    transition: transform 0.25s ease, box-shadow 0.25s ease;
    text-decoration: none;
}

.circle-btn:hover {
    transform: translateY(-6px) scale(1.05);
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.4);
}

/* Individual circle images */
.circle-btn.chicken {
    background-image: url('./36.jpg');
}

.circle-btn.sandwich {
    background-image: url('./37.jpeg');
}

.circle-btn.appetizer {
    background-image: url('./38.jpeg');
}

.circle-btn.drinks {
    background-image: url('./39.jpeg');
}

.circle-btn.address {
    background-image: url('./8.jpeg');
}

.circle-btn.wajbet {
    background-image: url('./41.jpeg');
}

/* Photo sections */
.photo-wrapper {
    width: 100%;
    max-width: 700px;
    margin: 50px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.photo-title {
    font-size: 32px;
    color: #FFD700;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
}

.photo-section {
    width: 100%;
    height: auto;
    min-height: 500px;
    border-radius: 15px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

/* Individual photo backgrounds */
.photo-section.chicken {
    background-image: url('./6.png');
}

.photo-section.sandwich {
    background-image: url('./2.png');
}

.photo-section.appetizer {
    background-image: url('./3.png');
}

.photo-section.drinks {
    background-image: url('./39.jpeg');
}

.photo-section.address {
    background-image: url('./Picture1.png');
}

.photo-section.wajbet {
    background-image: url('./4.png');
}

/* Mobile hint text */
.mobile-hint {
    font-size: 18px;
    color: white;
    font-weight: 700;
    margin-top: 20px;
    text-align: center;
    background: rgba(255, 255, 255, 0.1);
    padding: 6px 14px;
    border-radius: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

/* Smooth scroll */
html {
    scroll-behavior: smooth;
}

/* Footer */
footer {
    width: 100%;
    background-color: #8b0000;
    color: white;
    padding: 20px 24px;
    font-family: 'Tajawal', sans-serif;
    margin-top: 40px;
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
}

.footer-left,
.footer-center,
.footer-right {
    flex: 1 1 200px;
}

.footer-left h3 {
    margin-bottom: 10px;
    font-size: 20px;
}

.footer-center p {
    margin: 5px 0;
}

.footer-right p {
    margin-bottom: 10px;
}

.social-icons {
    display: flex;
    justify-content: center;
    gap: 16px;
    margin-top: 10px;
}

.social-icons a {
    display: inline-block;
    width: 40px;
    height: 40px;
    background-size: cover;
    background-position: center;
    border-radius: 50%;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.facebook-icon {
    background-image: url('./124.png');
}

.instagram-icon {
    background-image: url('./123.png');
}

.social-icons a:hover {
    transform: scale(1.15);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.6);
}

.footer-bottom {
    text-align: center;
    margin-top: 15px;
    font-size: 14px;
    color: #ffe6e6;
}

/* Responsive */
@media (max-width: 900px) {
    .photo-section {
        min-height: 400px;
    }
}

@media (max-width: 600px) {
    .brand h1 {
        font-size: 38px;
    }

    .brand .tag {
        font-size: 20px;
    }

    .brand h3 {
        font-size: 18px;
    }

    .circle-list {
        flex-direction: column;
        gap: 20px;
    }

    .circle-btn {
        width: 100px;
        height: 100px;
    }

    .mobile-hint {
        font-size: 16px;
    }

    .photo-section {
        width: 90%;
        min-height: 300px;
    }

    .footer-content {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}